@font-face {
  font-family: "OpenSans";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("/fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("/fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
html,
body {
  margin: 0;
  padding: 0;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: #000;
}
.access-label {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.logo::before {
  background-image: url("../img/assets/mishka-logo-mobile.svg");
  content: "";
  width: 85px;
  height: 35px;
  display: block;
}
.main-nav {
  padding: 0;
  margin: 0;
  list-style: none;
}
.page-header__subject {
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000;
  background-image: url("../img/content/1-hero-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  display: flex;
  width: 100%;
  height: 283px;
  justify-content: center;
  align-items: center;
}
.page-header__subject--color-bg {
  background-color: #F1F1F1;
  background-image: none;
}
.toggle-burger {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  border: none;
  padding: 0;
  box-sizing: border-box;
  background: none;
  cursor: pointer;
  width: 20px;
  height: 14px;
}
.toggle-burger::after,
.toggle-burger::before {
  content: "";
  width: 100%;
  display: inline-block;
  background-color: #231F20;
  height: 2px;
}
.toggle-burger__line {
  width: 100%;
  display: inline-block;
  background-color: #231F20;
  height: 2px;
}
.heading-link {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000;
  text-decoration: none;
}
.heading-link--icon-search::before,
.heading-link--icon-cart::before {
  content: "";
  width: 18px;
  height: 18px;
  display: inline-block;
}
.heading-link--icon-search::before {
  background-image: url("../img/assets/magnifier.svg");
}
.heading-link--icon-cart::before {
  width: 21px;
  background-image: url("../img/assets/cart.svg");
}
.heading-link--icon-htmlacademy::after {
  content: "";
  width: 26.9px;
  height: 34.1px;
  display: inline-block;
  background-image: url("../img/assets/copyright.svg");
}
.catalog-nav {
  list-style: none;
  margin: 0;
  padding: 0;
}
.catalog-nav__item::after {
  content: "";
  width: 60px;
  height: 60px;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 60px 60px;
}
.catalog-nav__item:nth-child(1) {
  background-color: hsl(168, 54%, 60%);
}
.catalog-nav__item:nth-child(1)::after {
  background-image: url("../img/content/1-section-interior.svg");
  background-size: 40px 60px;
  width: 40px;
}
.catalog-nav__item:nth-child(2) {
  background-color: hsl(174, 42%, 50%);
}
.catalog-nav__item:nth-child(2)::after {
  background-image: url("../img/content/2-section-toys.svg");
}
.catalog-nav__link {
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #FFF;
  text-decoration: none;
}
.product-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.product-preview {
  display: flex;
  flex-direction: column;
}
.product-preview__anchor {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-decoration: none;
}
.product-preview__image {
  display: block;
}
.product-preview__name {
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000;
}
.product-preview__props {
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 30px;
  color: #444;
}
.product-preview__price {
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: #000;
}
.product-preview__order-key {
  align-self: flex-start;
  background: none;
  padding: 0;
  margin: 0;
  border: none;
  display: inline-block;
}
.product-preview__order-key::before {
  content: url("../img/assets/cart.svg");
}
.video-modal__heading {
  margin: 0;
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000;
}
.video-modal__heading::before {
  content: url("../img/assets/camera.svg");
  display: inline-block;
}
.video-modal__placeholder {
  --bg-img: linear-gradient(45deg, hsl(168, 54%, 60%), #F1F1F1 25%);
  background-image: var(--bg-img);
  background-color: #F1F1F1;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 150px;
  border: none;
  cursor: pointer;
}
.video-modal__placeholder::before {
  content: url("../img/assets/play-btn.svg");
  display: inline-block;
}
.video-modal__announce {
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 30px;
  color: #444;
  margin: 0;
}
.outline-btn {
  display: block;
  background-color: transparent;
  border: 2px solid #444;
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444;
  text-transform: uppercase;
}
a.outline-btn {
  text-decoration: none;
}
.primary-btn {
  background-color: hsl(168, 54%, 60%);
  padding: 15px;
  margin: 0;
  border: none;
  text-transform: uppercase;
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #FFF;
}
.page-footer {
  display: grid;
  grid-template-columns: 30px 1fr 30px;
  background-color: #F1F1F1;
  grid-template-rows: auto auto;
}
.page-footer__social-icons-list {
  grid-column: 2/3;
  grid-row: 1/2;
}
.page-footer__heading-link {
  display: inline-block;
  grid-column: 2/3;
  grid-row: 1/2;
  grid-row: 2/3;
  justify-self: center;
  font-size: 19px;
  line-height: 18px;
}
.social-icons-list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
}
.social-icons-list__item:nth-child(1) .social-icons-list__anchor::before {
  content: url("../img/assets/contact-tg.svg");
}
.social-icons-list__item:nth-child(2) .social-icons-list__anchor::after {
  content: url("../img/assets/contact-vk.svg");
}
.social-icons-list__item:nth-child(3) .social-icons-list__anchor::before {
  content: url("../img/assets/contact-youtube.svg");
}
.social-icons-list__anchor::before {
  display: inline-block;
}
.form__input-notice {
  color: #444;
}
.form__input--selectboxes {
  display: grid;
  flex-direction: column;
}
.form__title {
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000;
  text-transform: uppercase;
}
.order-dialog {
  display: grid;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
.order-dialog__overlay {
  height: 100%;
  width: 100%;
  opacity: 0.9;
  grid-column: 1/2;
  grid-row: 1/2;
}
.order-dialog__form {
  grid-column: 1/2;
  grid-row: 1/2;
  z-index: 1;
  justify-self: center;
  align-self: center;
}
.boxes-select {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  justify-content: space-between;
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000;
}
.box-option__input-label {
  display: inline-block;
  border: 2px solid rgba(68, 68, 68, 0.1);
  text-transform: uppercase;
  color: #444;
}
.box-option__input-source:checked + .box-option__input-label {
  border: 2px solid hsl(168, 54%, 60%);
}
